import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { HttpModule } from '@angular/http';
import {AppRoutingModule} from "./app.routing-module";
import {NgbModule} from "@ng-bootstrap/ng-bootstrap";
import {RecipesModule} from "./recipes/recipes.module";
import {ShoppingListModule} from "./shopping-list/shopping-list.module";

import { AppComponent } from './app.component';
import { HeaderComponent } from './shared/header.component';
import { ProtectedComponent } from './protect/protected/protected.component';
import { SigninComponent } from './protect/unprotected/signin.component';
import { SignupComponent } from './protect/unprotected/signup.component';
import {ModalBasicComponent} from "./modal-basic.component";

import {AuthService} from "./shared/auth.service";
import {AuthGuard} from "./shared/auth.guard";
import {RecipeService} from "./recipes/recipe.service";
import {ShoppingListService} from "./shopping-list/shopping-list.service";

import {DropdownDirective} from "./shared/dropdown.directive";

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    ModalBasicComponent,
    ProtectedComponent,
    SigninComponent,
    SignupComponent,
    DropdownDirective
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    AppRoutingModule,
    RecipesModule,
    ShoppingListModule,
    NgbModule.forRoot()
  ],
  providers: [
    AuthService,
    AuthGuard,
    RecipeService,
    ShoppingListService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



