import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from "@angular/forms";

import {NgbModal} from '@ng-bootstrap/ng-bootstrap';

import {AuthService} from "./shared/auth.service";
import {RecipeService} from "./recipes/recipe.service";

@Component({
  selector: 'app-modal-basic',
  template: `

    <div class="container" *ngIf="!isAuth()">
      <div class="col-xs-12 col-sm-10 col-md-7 col-lg-5 col-xl-4 show-modal">
        <img src="yoda.png">
        <p>
          <strong>Приветствую тебя юный Джедай</strong>
        </p>
        <hr>
        <button class="btn btn-lg btn-outline-success" (click)="open(content)" *ngIf="!isAuth()" style="cursor: pointer;">Войти</button>
        
        <template ngbModalContainer></template>
      </div>
    </div>

    <template #content let-c="close" let-d="dismiss">
    <form [formGroup]="myForm" (ngSubmit)="onSignin()">
      <div class="modal-header">
        <h4 class="modal-title">Введите данные</h4>
        <button type="button" class="close" aria-label="Close" (click)="d('Cross click')">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        <div class="form-group">
          <label for="email">E-Mail</label>
          <input formControlName="email" type="email" id="email" #email class="form-control">
          <span *ngIf="!email.pristine && email.errors != null && email.errors['noEmail']">Invalid mail address</span>
        </div>
        <div class="form-group">
          <label for="password">Пароль</label>
          <input formControlName="password" type="password" id="password" class="form-control">
        </div>
        <div class="form-group">
          <label for="confirm-password">Повторить пароль</label>
          <input formControlName="confirmPassword" type="password" id="confirm-password" #confirmPassword class="form-control">
          <span *ngIf="!confirmPassword.pristine && confirmPassword.errors != null && confirmPassword.errors['passwordsNotMatch']">Пароли не совпадают</span>
        </div>
      </div>
      <div class="modal-footer">
        <button type="submit" [disabled]="!myForm.valid" class="btn btn-primary">Войти</button>
      </div>
     </form>
    </template>
    
    <!--<pre>{{closeResult}}</pre>-->
    
      <div class="container-fluid" *ngIf="isAuth()">
        <div class="col-xs-12 col-sm-10 col-md-7 col-lg-5 col-xl-4 show-modal">
          <img src="darth_vader.png">
          <p>Собирай коллекцию<br>и не забывай сохранять изменения</p>
        <hr>
          <div class="alert alert-warning" role="alert">
            <a (click)="onStore()" style="cursor: pointer;"><strong>СОХРАНИТЬ</strong></a>
          </div>
        <hr>
        <p>
          <button type="button" class="btn btn-danger" (click)="onLogout()" style="cursor: pointer;">Выход</button>
        </p>
        </div>
      </div>
    
    <router-outlet></router-outlet>
    
  `,
  styles: ['.show-modal {text-align: center; font-size: 1.2em; margin: 150px auto; }']
})
export class ModalBasicComponent {

  closeResult: string;

  myForm: FormGroup;
  error = false;
  errorMessage = '';

  constructor(private modalService: NgbModal,
              private fb: FormBuilder,
              private authService: AuthService,
              private recipeService: RecipeService) {}

  // onSignup() {
  //   this.authService.signupUser(this.myForm.value);
  // }

  onSignin() {
    this.authService.signinUser(this.myForm.value);
  }

  ngOnInit(): any {
    this.myForm = this.fb.group({
      email: ['', Validators.compose([
        Validators.required,
        this.isEmail
      ])],
      password: ['', Validators.required],
      confirmPassword: ['', Validators.compose([
        Validators.required,
        this.isEqualPassword.bind(this)
      ])],
    });
  }

  isEmail(control: FormControl): {[s: string]: boolean} {
    if (!control.value.match(/^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/)) {
      return {noEmail: true};
    }
  }

  isEqualPassword(control: FormControl): {[s: string]: boolean} {
    if (!this.myForm) {
      return {passwordsNotMatch: true};

    }
    if (control.value !== this.myForm.controls['password'].value) {
      return {passwordsNotMatch: true};
    }
  }

  open(content) {
    this.modalService.open(content)
  }

  // open(content) {
  //   this.modalService.open(content).result.then((result) => {
  //     this.closeResult = `Closed with: ${result}`;
  //   }, (reason) => {
  //     this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
  //   });
  // }
  //
  // private getDismissReason(reason: any): string {
  //   if (reason === ModalDismissReasons.ESC) {
  //     return 'by pressing ESC';
  //   } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
  //     return 'by clicking on a backdrop';
  //   } else {
  //     return  `with: ${reason}`;
  //   }
  // }

  onStore() {
    this.recipeService.storeData().subscribe(
      data => console.log(data),
      error => console.error(error)
    );
  }

  isAuth() {
    return this.authService.isAuthenticated();
  }

  onLogout() {
    this.authService.logout();
  }

}



