import { Component } from '@angular/core';

@Component({
  selector: 'app-recipe-start',
  template: `
      <br>
      <div class="card card-inverse card-info mb-3 text-center col-xs-12 col-sm-12 col-md-12 col-lg-11 detail-position">
        <div class="card-block"  style="padding: 0">
          <blockquote class="card-blockquote" style="padding: 3px">
            <h3>- Посмотри пример заполнения карточки героя</h3>
            <h3>- Удали пример заполнения</h3>
            <h3>- Нажми на кнопку "Добавить героя"</h3>
            <h3>- Заполни все необходимые поля и нажми на кнопку "Сохранить"</h3>
            <h3>- Чтобы управлять коллекцией воспользуйся пунктом меню</h3>
            <footer><cite title="Source Title">ты в можешь добавлять и удалять новых героев, загружать новые картинки, изменять их описания и характеристики</cite></footer>
          </blockquote>
        </div>
      </div>
  `,
  styles: ['.detail-position {margin: 0 auto;  padding: 0}']
})
export class RecipeStartComponent {

}



