import { Component } from '@angular/core';

import {RecipeService} from "../recipes/recipe.service";
import {AuthService} from "./auth.service";

@Component({
  selector: 'app-header',
  template: `
    <nav class="navbar navbar-toggleable-md navbar-inverse bg-inverse" *ngIf="isAuth()">
      <button class="navbar-toggler navbar-toggler-right"
              type="button" data-toggle="collapse"
              data-target="#navbarNavDropdown"
              aria-controls="navbarNavDropdown"
              aria-expanded="false"
              aria-label="Toggle navigation"
              style="cursor: pointer;">
        <span class="navbar-toggler-icon"></span>
      </button>
      <a class="navbar-brand" [routerLink]="['/']" style="font-weight: bold;">ГЛАВНАЯ</a>
      <div class="collapse navbar-collapse" id="navbarNavDropdown">
        <ul class="navbar-nav">
          <!--<li class="nav-item">-->
            <!--<a class="nav-link" [routerLink]="['signup']">Signup</a>-->
          <!--</li>-->
          <!--<li class="nav-item">-->
            <!--<a class="nav-link" [routerLink]="['signin']">Signin</a>-->
          <!--</li>-->
          <!--<li class="nav-item" routerLinkActive="active">-->
            <!--<a class="nav-link" [routerLink]="['/shopping-list']">Лучшие герои</a>-->
          <!--</li>-->
          <li class="nav-item" routerLinkActive="active">
            <a class="nav-link" [routerLink]="['/recipes']">Список героев</a>
          </li>
          <!--<li class="nav-item dropdown" appDropdown>-->
          <li class="nav-item dropdown" style="cursor: pointer;">
            <a class="nav-link dropdown-toggle"
               id="navbarDropdownMenuLink"
               data-toggle="dropdown"
               aria-haspopup="true"
               aria-expanded="false">
              Управление коллекцией
            </a>
            <!--<div class="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">-->
              <!--<a class="dropdown-item" href="#">Запомнить героев</a>-->
              <!--<a class="dropdown-item" href="#">Загрузить героев</a>-->
            <!--</div>-->
            <ul class="dropdown-menu" aria-labelledby="navbarDropdownMenuLink" style="right: 0; max-width: 180px">
              <a class="dropdown-item" (click)="onStore()" style="cursor: pointer; text-align: center">Сохранить</a>
              <a class="dropdown-item" (click)="onFetch()" style="cursor: pointer; text-align: center">Загрузить</a>
            </ul>
          </li>
        </ul>
      </div>
    </nav>
  `
})
export class HeaderComponent {

  constructor(private recipeService: RecipeService,
              private authService: AuthService) {}

  onStore() {
    this.recipeService.storeData().subscribe(
      data => console.log(data),
      error => console.error(error)
    );
  }

  onFetch() {
    this.recipeService.fetchData();
  }

  isAuth() {
    return this.authService.isAuthenticated();
  }

  // onLogout() {
  //   this.authService.logout();
  // }
}



